<template>
  <div>
    <label for="">Filtrar por telefono</label>
    <b-form-input
      v-model="searchQuery"
      placeholder="Buscar por telfono"
      class="mb-3"
    ></b-form-input>
    <b-table
      striped
      hover
      :items="filteredClientes"
      :fields="fields"
    ></b-table>
  </div>
</template>

<script>
import { ref, onMounted, computed } from "vue";
import { db } from '../firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, getDocs } from 'firebase/firestore';
import { comunidades } from '../data/comunidades';
import { provincias } from '../data/provincias';

export default {
  setup() {
    const clientes = ref([]);
    const formattedClientes = ref([]);
    const searchQuery = ref("");

    const fields = [
      { key: 'nombre', label: 'Nombre' },
      { key: 'apellidos', label: 'Apellidos' },
      { key: 'provincia', label: 'Provincia' },
      { key: 'correo', label: 'Correo' },
      { key: 'tel', label: 'Telfono' },
      { key: 'comunidad', label: 'Comunidad' },
      { key: 'poblacion', label: 'Poblacin' },
    ];

    const fetchData = async () => {
      clientes.value = (await getDocs(collection(db, 'especialistas'))).docs.map(doc => ({
        ...doc.data()
      }));
      formatClientes();
    };

    const formatClientes = () => {
      formattedClientes.value = clientes.value.map(cliente => {
        const formattedCliente = { ...cliente };
        console.log(formattedCliente.comunidad);
        formattedCliente.comunidad = comunidades[formattedCliente.comunidad];
        formattedCliente.provincia = provincias[formattedCliente.provincia];
        return formattedCliente;
      });
    };

    const filteredClientes = computed(() => {
      if (!searchQuery.value) {
        return formattedClientes.value;
      }
      return formattedClientes.value.filter(cliente =>
        cliente.tel.includes(searchQuery.value)
      );
    });

    onMounted(() => {
      fetchData();
    });

    return {
      formattedClientes,
      fields,
      searchQuery,
      filteredClientes,
    };
  }
};
</script>

