<template>
  <div>
    <b-alert v-if="showSuccess" variant="success" show>Success Alert</b-alert>
    <formulario-cliente @sendForm="recivedInfo" />
  </div>
</template>

<script>
import FormularioCliente from "@/components/FormularioCliente.vue";
import { ref } from "vue";
import { db } from '../firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, addDoc } from 'firebase/firestore';

export default {
  components: {
    FormularioCliente,
  },
  setup() {
    const showSuccess = ref(false);
    const user = ref({
      nombre: '',
      apellidos: '',
      correo: '',
      tel: '',
      comunidad: '',
      provincia: '',
      poblacion: '',
      casos: 0,
    });
    const caso = ref(null);

    const addUser = async () => {
      await addDoc(collection(db, 'clientes'), user.value);
      user.value = {
        nombre: '',
        apellidos: '',
        correo: '',
        tel: '',
        comunidad: '',
        provincia: '',
        poblacion: '',
        casos: 0,
      };
      showSuccess.value = true;
      setTimeout(() => {
        showSuccess.value = false;
      }, 2000);
    };

    const addCase = async () => {
        await addDoc(collection(db, 'casos'), caso);
        caso.value = null;
    };

    const recivedInfo = (receivedUser, receivedCase) => {
      if (receivedUser) {
          receivedCase.casos = 0;
          user.value = receivedUser;
          console.log('User received:', user.value);
          addUser();
          console.log('Recibo caso', receivedCase);
        if (receivedCase.especialidad !== '') {
          receivedCase.telCliente = receivedUser.tel;
          receivedCase.telEspecialista = null;
          receivedCase.status = '0';
          caso.value = receivedCase;
          addCase();
        }
      } else {
        console.error("Received user data is null or undefined");
      }
    };

    return {
      recivedInfo,
      showSuccess,
    };
  },
};
</script>

<style>
.custom-select-wrapper {
  position: relative;
  width: 300px;
}

.custom-select {
  appearance: none;
  width: 100%;
  padding: 10px 40px 10px 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fffcf5 !important;
  color: #66615b;
  font-size: 16px;
  cursor: pointer;
  outline: none;
  position: relative;
}

.custom-select:focus {
  border-color: #007BFF;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.custom-select-wrapper::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 25px;
  width: 10px;
  height: 10px;
  background.color: #fffcf5;
  clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  transform: translateY(-50%);
  pointer-events: none;
}
</style>
