<template>
  <div>
    <form @submit.prevent>
        <div class="row">
          <!-- <div class="col-md-6">
            <fg-input
            v-model="user.user"
              type="text"
              label="Usuario"
              placeholder="Usuario"
            >
            </fg-input>
          </div>
          <div class="col-md-6">
            <fg-input
            v-model="user.pass"
              type="text"
              label="Contrasea"
              placeholder="Contrasea"
            >
            </fg-input>
          </div> -->
          <div class="col-md-6">
            <fg-input
            v-model="user.nombre"
              type="text"
              label="Nombre"
              placeholder="Nombre"
            >
            </fg-input>
          </div>
          <div class="col-md-6">
            <fg-input
            v-model="user.apellidos"
              type="text"
              label="Apellidos"
              placeholder="Apellidos"
            >
            </fg-input>
          </div>
          <div class="col-md-6">
            <fg-input
            v-model="user.correo"
              type="text"
              label="Correo"
              placeholder="Correo"
            >
            </fg-input>
          </div>
          <div class="col-md-6">
            <fg-input
            v-model="user.tel"
              type="text"
              label="Telfono"
              placeholder="Telfono"
            >
            </fg-input>
          </div>
          <div class="custom-select-wrapper col-md-6">
                    <label for="">Tipo de especialidad</label>
                <select v-model="user.especialidades" class="custom-select" multiple>
                    <option v-for="esp in especialidades" :key="esp.id" :value="esp.id">{{ esp.nombre }}</option>
                </select>
          </div>
          <div class="custom-select-wrapper col-md-6">
                    <label for="">Comunidad Autonoma</label>
                <select v-model="user.comunidad" class="custom-select">
                    <option v-for="(com, index) in comunidades" :key="index" :value="index">{{ com }}</option>
                </select>
          </div>
          <div class="custom-select-wrapper col-md-6">
                    <label for="">Provincias</label>
                <select v-model="user.provincia" class="custom-select">
                    <option v-for="(pro, index) in provincias" :key="index" :value="index">{{ pro }}</option>
                </select>
          </div>
          <div class="custom-select-wrapper col-md-6">
                <fg-input
                v-model="user.poblacion"
                type="text"
                label="Poblacin"
                placeholder="Poblacin"
                />
            </div>

          <div class="col-12 text-center mt-3">
            <p-button type="info" round @click.native.prevent="createEspe">
                Dar de alta especialista
            </p-button>
          </div>
        </div>
      </form>
  </div>
</template>
<script>
import { ref, onMounted } from 'vue';
import { db } from '../firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, getDocs, addDoc } from 'firebase/firestore';
import { comunidades } from '../data/comunidades';
import { provincias } from '../data/provincias';

export default {
  setup() {
    const user = ref({
        nombre: '',
        apellidos: '',
        correo: '',
        tel: '',
        comunidad: '',
        provincia: '',
        poblacion: '',
        especialidades: [],
    })

    const especialidades = ref([])
    const fetchData = async () => {
      especialidades.value = (await getDocs(collection(db, 'especialidades'))).docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
    };

    const addEspecialidad = async () => {
      try {
        await addDoc(collection(db, 'especialistas'), user.value);
        user.value = {
        nombre: '',
        apellidos: '',
        correo: '',
        tel: '',
        comunidad: '',
        provincia: '',
        poblacion: '',
        especialidades: [],
      };
      } catch (error) {
        console.error("Error adding document: ", error);
        // mensaje.value = 'Error al aadir la especialidad.';
      }
    }

    onMounted(() => {
      fetchData();
    });

    return {
      user,
      comunidades,
      provincias,
      especialidades,
      createEspe: (() => {
        addEspecialidad();
      })
    };
  },
};
</script>

<style>
.custom-select-wrapper {
    position: relative;
    width: 300px;
}

.custom-select {
    appearance: none;
    width: 100%;
    padding: 10px 40px 10px 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fffcf5 !important;
    color: #66615b;
    font-size: 16px;
    cursor: pointer;
    outline: none;
    position: relative;
}

.custom-select:focus {
    border-color: #007BFF;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.custom-select-wrapper::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 25px;
    width: 10px;
    height: 10px;
    background.color: #fffcf5;
    clip-path: polygon(100% 0%, 0 0%, 50% 100%);
    transform: translateY(-50%);
    pointer-events: none;
}
</style>
