<template>
  <div :class="{ 'nav-open': $sidebar.showSidebar }">
    <notifications></notifications>
    <router-view></router-view>
  </div>
</template>

<script>
import { ref } from "vue";
import { db } from './firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, query, where, onSnapshot  } from 'firebase/firestore';

export default {
  setup() {
    const casosSinAsignar = ref(0)
    const casosSinPresupuesto = ref(0)

    function mostrarNotificacion(type) {
      if (Notification.permission === 'granted') {
        const titulo = type === 1 ? 'Caso sin asignar' : 'Cliente sin presupuesto'
        const desc = type === 1 ? 'Debes asignar un especialista' : 'Se debe enviar un presupuesto al cliente'
        const notification = new Notification(titulo, {
          body: desc,
          appName: 'Informes mdicos periciales',
          icon: './src/img/vue-logo.png' // Ruta al cono opcional
        });

        notification.onclick = function(tp) {
          console.log('Notificacin clicada', tp);
          // Puedes agregar acciones adicionales al hacer clic en la notificacin aqu
        };
      } else if (Notification.permission !== 'denied') {
        Notification.requestPermission().then(permission => {
          if (permission === 'granted') {
            this.mostrarNotificacion();
          }
        });
      }
    }
    const q = query(collection(db, 'casos'), where('telEspecialista', '==', null), where('status', '==', '0'));
          
          // Escuchar cambios en tiempo real
          onSnapshot(q, (querySnapshot) => {
            if (querySnapshot.docs.length > casosSinAsignar.value) {
              mostrarNotificacion(1);
            }
            casosSinAsignar.value = querySnapshot.docs.length;
          });

    const w = query(collection(db, 'casos'), where('status', '==', '1'));
    onSnapshot(w, (querySnapshot) => {
      if (querySnapshot.docs.length > casosSinPresupuesto.value) {
        mostrarNotificacion(2);
      }
      casosSinPresupuesto.value = querySnapshot.docs.length;
    });
  }
};
</script>

<style lang="scss">
.vue-notifyjs.notifications {
  .alert {
    z-index: 10000;
  }
  .list-move {
    transition: transform 0.3s, opacity 0.4s;
  }
  .list-item {
    display: inline-block;
    margin-right: 10px;
  }
  .list-enter-active {
    transition: transform 0.2s ease-in, opacity 0.4s ease-in;
  }
  .list-leave-active {
    transition: transform 1s ease-out, opacity 0.4s ease-out;
  }

  .list-enter {
    opacity: 0;
    transform: scale(1.1);
  }
  .list-leave-to {
    opacity: 0;
    transform: scale(1.2, 0.7);
  }
}
</style>
