<template>
  <div>
    <form @submit.prevent>
        <div class="row">
          <div class="col-md-12">
            <fg-input
            v-model="especialidad"
              type="text"
              label="Nombre especialidad"
              placeholder="Especialidad"
            >
            </fg-input>
          </div>
          
          <div class="col-12 text-center mt-4">
            <p-button type="info" round @click.native.prevent="createEspe">
                Dar de alta especialidad
            </p-button>
          </div>
        </div>
      </form>
  </div>
</template>
<script>
import { StatsCard, ChartCard } from "@/components/index";
import { ref } from "vue";
import { db } from '../firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, addDoc  } from 'firebase/firestore';
import NotificationTemplate from "./Notifications/NotificationTemplate";

export default {
  components: {
    StatsCard,
    ChartCard,
  },
  /**
   * Chart data used to render stats, charts. Should be replaced with server data
   */
  setup() {
    const especialidad = ref('');

    const notifyVue = (verticalAlign, horizontalAlign) => {
      this.$notify({
        component: NotificationTemplate,
        icon: "ti-check",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: 'success',
      });
    }; 

    const addEspecialidad = async () => {
      try {
        await addDoc(collection(db, 'especialidades'), {
          nombre: especialidad.value,
        });
        especialidad.value = '';
        notifyVue('top', 'left');
      } catch (error) {
        console.error("Error adding document: ", error);
        // mensaje.value = 'Error al aadir la especialidad.';
      }
    };

    return {
      especialidad,
      createEspe: (() => {
        addEspecialidad();
      })
    };
  },
};
</script>
<style>
.custom-select-wrapper {
    position: relative;
    width: 300px;
}

.custom-select {
    appearance: none;
    width: 100%;
    padding: 10px 40px 10px 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fffcf5 !important;
    color: #66615b;
    font-size: 16px;
    cursor: pointer;
    outline: none;
    position: relative;
}

.custom-select:focus {
    border-color: #007BFF;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.custom-select-wrapper::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 25px;
    width: 10px;
    height: 10px;
    background.color: #fffcf5;
    clip-path: polygon(100% 0%, 0 0%, 50% 100%);
    transform: translateY(-50%);
    pointer-events: none;
}
</style>
