<template>
  <div>
    <b-table
      v-if="casos.length > 0"
      striped
      hover
      :items="casos"
      :fields="fields"
    >
      <template #cell(actions)="data" class="text-center">
        <b-button size="sm" @click="openBudgetModal(data.item)">Enviar presupuesto</b-button>
      </template>
    </b-table>
    <span v-else>No hay casos sin asignar especialista</span>
    
    <!-- Modal para enviar presupuesto -->
    <b-modal v-model="modalMail" id="budget-modal" hide-footer>
      <template #modal-title>
        Enviar presupuesto
      </template>
      <div>
        <label for="">Presupuesto</label> <br>
        <input v-model="presupuesto"  />
      </div> <br>
      <div>
        <textarea v-model="body" placeholder="Escribe el mensaje del presupuesto" style="width:100%;" rows="6"></textarea>
      </div>
      <div>
        <input type="file" @change="onFileChange" />
      </div>
      <div class="text-center mt-4">
        <b-button @click="sendBudget">Enviar</b-button>
      </div>
    </b-modal>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import axios from 'axios'; // Importa axios para realizar peticiones HTTP
import { db } from '../firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, getDocs, doc, updateDoc, query, where } from 'firebase/firestore';

export default {
  setup() {
    const casos = ref([]);
    const especialistas = ref([]);
    const searchQuery = ref("");
    const espeSelect = ref("");
    const caseWantSet = ref(null);
    const clientToEdit = ref({});
    const modalShow = ref(false);
    const modalCase = ref(false);
    const modalMail = ref(false); // Nuevo estado para el modal de presupuesto
    const presupuesto = ref(null);
    const body = ref(''); // Estado para el mensaje del presupuesto
    const filePresu = ref(null); // Estado para el archivo adjunto

    const fields = [
      { key: 'nombreCliente', label: 'Nombre Cliente' },
      { key: 'apellidosCliente', label: 'Apellidos Cliente' },
      { key: 'telCliente', label: 'Telefono Cliente' },
      { key: 'actions', label: 'Acciones' }
    ];

    const searchCases = async () => {
      try {
        const q = query(collection(db, 'casos'), where('status', '==', '1'));
        const querySnapshot = await getDocs(q);
        const casosData = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));

        // Obtener datos de clientes
        await Promise.all(casosData.map(async (caso) => {
          const clientQuery = query(collection(db, 'clientes'), where('tel', '==', caso.telCliente));
          const clientSnapshot = await getDocs(clientQuery);
          const cliente = clientSnapshot.docs.map(doc => doc.data())[0];
          caso.nombreCliente = cliente ? cliente.nombre : 'N/A';
          caso.apellidosCliente = cliente ? cliente.apellidos : 'N/A';
          caso.correo = cliente ? cliente.correo : 'N/A';
        }));

        casos.value = casosData;
      } catch (error) {
        console.error('Error al buscar casos:', error);
      }
    };

    const searchClient = () => {
      console.log('Llegooo')
    };

    const updateCase = async () => {
      const clienteDocRef = doc(db, 'casos', caseWantSet.value.id);
      await updateDoc(clienteDocRef, caseWantSet.value);
      searchCases();
      modalCase.value = false;
    };

    const onFileChange = (event) => {
      const file = event.target.files[0];
      console.log(file)
      const reader = new FileReader();
      reader.onloadend = () => {
        filePresu.value = {
          content: reader.result.split(',')[1], // Obtener base64
          filename: file.name,
          type: file.type
        };
      };
      reader.readAsDataURL(file);
    };

    const sendBudget = async () => {
      try {
        const emailData = {
          to: caseWantSet.value.correo, // Cambia esto a la direccin de correo que desees
          subject: 'Presupuesto',
          text: body.value,
          attachments: filePresu.value ? [filePresu.value] : []
        };
        const result = await axios.post('http://localhost:3000/send-email', emailData);
        if (result.data.success) {
          alert('Presupuesto enviado correctamente');
          modalMail.value = false;
        } else {
          alert('Hubo un error al enviar el presupuesto: ' + result.data.error);
        }
      } catch (error) {
        console.error('Error al enviar el presupuesto:', error);
        alert('Hubo un error al enviar el presupuesto');
      }
    };

    onMounted(() => {
      searchCases();
    });

    return {
      casos,
      fields,
      searchQuery,
      modalShow,
      clientToEdit,
      modalCase,
      modalMail, // Nuevo estado del modal
      espeSelect,
      especialistas,
      body, // Nuevo estado del mensaje
      filePresu, // Nuevo estado del archivo
      presupuesto,
      openBudgetModal: (c) => {
        caseWantSet.value = c;
        console.log(caseWantSet.value);
        modalMail.value = true;
      },
      setEspe: () => {
        caseWantSet.value.telEspecialista = espeSelect.value;
        caseWantSet.value.status = '1';
        caseWantSet.value.presupuesto = '';
        updateCase();
      },
      onFileChange,
      sendBudget
    };
  }
};
</script>
