<template>
  <div>
    <label for="">Filtrar por telefono</label>
    <b-form-input
      v-model="searchQuery"
      placeholder="Buscar por telfono"
      class="mb-3"
    ></b-form-input>
    <b-table
      striped
      hover
      :items="filteredClientes"
      :fields="fields"
    >
      <template #cell(actions)="data" class="text-center">
        <b-button size="sm" @click="editClient(data.item)">Editar cliente</b-button>
        <b-button class="my-3 mx-3" size="sm" @click="$router.push({ name: 'UserProfile', params: { id: data.item.id } })">Ver cliente</b-button>
        <b-button size="sm" @click="newCase(data.item.tel)">Agregar caso</b-button>
      </template>
    </b-table>
    <b-modal v-model="modalShow" id="bv-modal-example" hide-footer>
      <template #modal-title>
        Editar cliente
      </template>
      <formulario-cliente @sendForm="recivedInfo" :client="clientToEdit" />
    </b-modal>
    <b-modal v-model="modalCase" id="bv-modal-example" hide-footer>
      <template #modal-title>
        Agregar caso
      </template>
      <formulario-caso @sendCase="recivedCase" />
    </b-modal>
  </div>
</template>

<script>
import { ref, onMounted, computed } from "vue";
import { db, messaging, getToken } from '../firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, getDocs, doc, updateDoc, addDoc } from 'firebase/firestore';
import FormularioCliente from "@/components/FormularioCliente.vue";
import FormularioCaso from "@/components/FormularioCaso.vue";
import { comunidades } from '../data/comunidades';
import { provincias } from '../data/provincias';
import axios from 'axios';

export default {
  components: {
    FormularioCliente,
    FormularioCaso,
  },
  setup() {
    const clientes = ref([]);
    const formattedClientes = ref([]);
    const searchQuery = ref("");
    const clientToEdit = ref({});
    const clientEdited = ref({});
    const modalShow = ref(false);
    const modalCase = ref(false);
    const telCase = ref(null);

    const fields = [
      { key: 'nombre', label: 'Nombre' },
      { key: 'apellidos', label: 'Apellidos' },
      { key: 'provincia', label: 'Provincia' },
      { key: 'correo', label: 'Correo' },
      { key: 'tel', label: 'Telfono' },
      { key: 'comunidad', label: 'Comunidad' },
      { key: 'poblacion', label: 'Poblacin' },
      { key: 'actions', label: 'Acciones' }
    ];

    const fetchData = async () => {
      clientes.value = (await getDocs(collection(db, 'clientes'))).docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      formatClientes();
    };

    const formatClientes = () => {
      formattedClientes.value = clientes.value.map(cliente => {
        const formattedCliente = { ...cliente };
        formattedCliente.comunidad = comunidades[formattedCliente.comunidad];
        formattedCliente.provincia = provincias[formattedCliente.provincia];
        formattedCliente.id = cliente.id;
        return formattedCliente;
      });
    };

    const filteredClientes = computed(() => {
      if (!searchQuery.value) {
        return formattedClientes.value;
      }
      return formattedClientes.value.filter(cliente =>
        cliente.tel.includes(searchQuery.value)
      );
    });

    const editClient = (client) => {
      clientToEdit.value = client;
      modalShow.value = true;
    };

    const saveChanges = async () => {
      try {
        const clienteDocRef = doc(db, 'clientes', clientToEdit.value.id);
        await updateDoc(clienteDocRef, clientEdited.value);
        fetchData();
        modalShow.value = false;
      } catch (error) {
        console.error('Error al actualizar el cliente:', error);
      }
    };

    const recivedInfo = (receivedUser) => {
      clientEdited.value = receivedUser;
      saveChanges();
    };

    const recivedCase = async (receivedCase) => {
      receivedCase.telCliente = telCase.value;
      receivedCase.telEspecialista = null;
      receivedCase.status = '0';
      await addDoc(collection(db, 'casos'), receivedCase);
      telCase.value = null;
      modalCase.value = false;
    };

    onMounted(() => {
      fetchData();
    });

    return {
      formattedClientes,
      fields,
      searchQuery,
      filteredClientes,
      editClient,
      modalShow,
      clientToEdit,
      recivedInfo,
      recivedCase,
      modalCase,
      newCase: (tel) => {
        telCase.value = tel;
        modalCase.value = true;
      },
    };
  }
};
</script>
