<template>
  <div>
    <b-table
      v-if="casos.length > 0"
      striped
      hover
      :items="casos"
      :fields="fields"
    >
      <template #cell(actions)="data" class="text-center">
        <b-button size="sm" @click="newCase(data.item)">Agregar especialista</b-button>
      </template>
    </b-table>
    <span v-else>No hay casos sin asignar especialista</span>
    <b-modal v-model="modalCase" id="bv-modal-example" hide-footer>
      <template #modal-title>
        Agregar especialista
      </template>
      <div class="custom-select-wrapper col-md-12">
        <label for="">Especialistas</label>
        <select v-model="espeSelect" class="custom-select">
          <option v-for="espe in especialistas" :key="espe.tel" :value="espe.tel">{{ espe.nombre + ' ' + espe.apellidos }}</option>
        </select>
      </div>
      <div class="col-12 text-center mt-4">
        <p-button type="info" round @click.native.prevent="setEspe">
          Asignar
        </p-button>
      </div>
    </b-modal>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import { db } from '../firebase'; // Asegrate de ajustar la ruta segn tu estructura de archivos
import { collection, getDocs, doc, updateDoc, query, where } from 'firebase/firestore';
import FormularioCliente from "@/components/FormularioCliente.vue";
import FormularioCaso from "@/components/FormularioCaso.vue";

export default {
  components: {
    FormularioCliente,
    FormularioCaso,
  },
  setup() {
    const casos = ref([]);
    const especialistas = ref([]);
    const searchQuery = ref("");
    const espeSelect = ref("");
    const caseWantSet = ref(null);
    const clientToEdit = ref({});
    const modalShow = ref(false);
    const modalCase = ref(false);
    const telCase = ref(null);

    const fields = [
      { key: 'nombreCliente', label: 'Nombre Cliente' },
      { key: 'apellidosCliente', label: 'Apellidos Cliente' },
      { key: 'telCliente', label: 'Telefono Cliente' },
      { key: 'actions', label: 'Acciones' }
    ];

    const editClient = (client) => {
      clientToEdit.value = client;
      modalShow.value = true;
    };

    const searchCases = async () => {
      try {
        const q = query(collection(db, 'casos'), where('telEspecialista', '==', null), where('status', '==', '0'));
        const querySnapshot = await getDocs(q);
        const casosData = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));

        // Obtener datos de clientes
        await Promise.all(casosData.map(async (caso) => {
          const clientQuery = query(collection(db, 'clientes'), where('tel', '==', caso.telCliente));
          const clientSnapshot = await getDocs(clientQuery);
          const cliente = clientSnapshot.docs.map(doc => doc.data())[0];
          caso.nombreCliente = cliente ? cliente.nombre : 'N/A';
          caso.apellidosCliente = cliente ? cliente.apellidos : 'N/A';
        }));

        casos.value = casosData;
      } catch (error) {
        console.error('Error al buscar casos:', error);
      }
    };

    const searchClient = async () => {
      try {
        const w = query(collection(db, 'especialistas'), where('especialidades', 'array-contains', casos.value[0].especialidad));
        const querySnap = await getDocs(w);
        const espes = querySnap.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        especialistas.value = espes;
        modalCase.value = true;
      } catch (error) {
        console.error('Error al buscar especialistas:', error);
      }
    };

    const updateCase = async () => {
      const clienteDocRef = doc(db, 'casos', caseWantSet.value.id);
      await updateDoc(clienteDocRef, caseWantSet.value);
      searchCases();
      modalCase.value = false;
    };

    onMounted(() => {
      searchCases();
    });

    return {
      casos,
      fields,
      searchQuery,
      editClient,
      modalShow,
      clientToEdit,
      modalCase,
      espeSelect,
      especialistas,
      newCase: (c) => {
        telCase.value = c.telCliente;
        caseWantSet.value = c;
        searchClient();
      },
      setEspe: () => {
        caseWantSet.value.telEspecialista = espeSelect.value;
        caseWantSet.value.status = '1';
        caseWantSet.value.presupuesto = '';
        updateCase();
      }
    };
  }
};
</script>
